interface ButtonProps {
    onClick: () => void;
    disabled?: boolean;
    className?: string;
    children: React.ReactNode;
}

import React from 'react';
 const Button: React.FC<ButtonProps> = ({ onClick, disabled, className, children }) => {
    return (
        <button
            style={{
                color: 'yellow',
                background: 'blue',
                height: 50,
                width: 50
            }}
            name="myButtonName"
            onClick={onClick}
            disabled={disabled}
            className={className}
        >
            {children}
        </button>
    );
};

export default Button;